// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------

footer {
  &.footer-listicle {
    height: 64px;
    width: 100%;
    background-color: #efefef;
    color: $brand-color;
    padding: 0 64px;
    .footer__nav {
      font-family: $text-font-1;
      float: right;
      > ul {
        display: block;
        list-style: none;
        padding: 0;
        margin: 0;
        font-size: 0;
        height: 64px;
        > li {
          display: inline-block;
          height: 64px;
          line-height: 64px;
          &:not(:last-child) {
            margin-right: 66px;
          }
          > a {
            font-size: 15px;
            font-weight: 400;
          }
        }
      }
    }
    .footer__copy {
      float: left;
      font-size: 15px;
      font-weight: 300;
      line-height: 64px;
    }
  }
}

.footer.landing {
  position: relative;
  display: block;
  height: 64px;
  width: 100%;
  background-color: transparent;
  color: #ffffff;
  padding: 0 64px;
  .footer__nav {
    font-family: $text-font-1;
    float: right;
    > ul {
      display: block;
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 0;
      height: 64px;
      > li {
        display: inline-block;
        height: 64px;
        line-height: 64px;
        &:not(:last-child) {
          margin-right: 64px;
        }
        > a {
          color: #ffffff;
          font-size: 15px;
          font-weight: 400;
          transition: all .2s ease;
          &:hover {
            color: #f58927;
          }
          &:focus {
            color: #d94800;
          }
        }
      }
    }
  }
  .footer__copy {
    float: left;
    font-size: 15px;
    font-weight: 300;
    line-height: 64px;
  }
  @media screen and (max-width: 768px) {
    display: none;
  }
}