// -----------------------------------------------------------------------------
// This file contains styles that are specific to the listicle page.
// -----------------------------------------------------------------------------

.listicle {
  @at-root #{&}__header {
    padding: 46px 0 63px 0;
    @at-root #{&}__title {
      font-family: $text-font-1;
      font-size: 40px;
      font-weight: 700;
      color: $brand-color;
      padding-bottom: 56px;
    }
    @at-root #{&}__detail {
      padding: 0 16px;
      .info {
        @at-root #{&}__icon {
          height: 30px;
          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain;
          margin-bottom: 20px;
          &.when {
            width: 30px;
            background-image: url(../images/icon/icon-date-purple.svg);
          }
          &.where {
            width: 26px;
            background-image: url(../images/icon/icon-location-purple.svg);
          }
          &.likes {
            width: 26px;
            background-image: url(../images/icon/icon-like-purple.svg);
          }
        }
        @at-root #{&}__text {
          padding-right: 32px;
          font-size: 24px;
          font-weight: 500;
        }
      }
    }
  }

  @at-root #{&}__desc {
    position: relative;
    max-height: 400px;
    overflow: hidden;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    ol {
      counter-reset: i 0;
      padding: 0;
      margin: 0;
      li {
        position: relative;
        padding: 50px 0;
        list-style-type: none;
        &:before {
          content: counter(i) ".";
          counter-increment: i;
          padding-right: 0.5em;
          font-family: $text-font-1;
          font-size: 30px;
          font-weight: 700;
          color: $brand-color;
        }
        &:not(:last-child) {
          &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: 0;
            left: 0;
            background-color: #f58927;
            background-image: linear-gradient(to left, #160144 0%, #f58927 100%);
            opacity: 0.5;
          }
        }
      }
    }
    font[size="5"] {
      font-family: $text-font-1;
      font-size: 30px;
      font-weight: 700;
      color: $brand-color;
    }
    img {
      width: 100%;
    }
  }

  @at-root #{&}__app {
    position: relative;
    width: calc(100% + 98px);
    height: 411px;
    margin-left: -48px;
    margin-bottom: 40px;
    padding: 71px 46px 0 46px;
    background-color: #160144;
    border-radius: 10px;
    box-shadow: 0 10px 76px 3px rgba(0, 0, 0, 0.48);
    @at-root #{&}__logo {
      width: 238px;
      height: 59px;
      background-image: url(../images/icon/Header-Logo-Horizontal.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      margin-bottom: 66px;
    }
    @at-root #{&}__dl-text {
      width: 62%;
      font-size: 20px;
      font-weight: 400;
      line-height: 30px;
      color: #ffffff;
      margin-bottom: 30px;
    }
    @at-root #{&}__dl-icon {
      font-size: 0;
      .appstore {
        display: inline-block;
        vertical-align: middle;
        width: 205px;
        height: 60px;
        background-image: url(../images/icon/Button-Appstore.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        margin-right: 15px;
      }
      .googleplay{
        display: inline-block;
        vertical-align: middle;
        width: 205px;
        height: 60px;
        background-image: url(../images/icon/Button-Playstore.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
      }
    }
    @at-root #{&}__img {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      width: 190px;
      height: 350px;
      background-image: url(../images/icon/Cover-Handphone.png);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
    }
  }

  @at-root #{&}__rewards {
    @at-root #{&}__title {
      font-family: $text-font-1;
      font-size: 30px;
      font-weight: 700;
      color: $brand-color;
      margin-bottom: 30px;
    }
    @at-root #{&}__list {
      position: relative;
      margin-bottom: 64px;
      width: calc(100% + 24px);
      margin-left: -12px;
      .swiper-button-prev, .swiper-button-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        z-index: 10;
        cursor: pointer;
        width: 24px;
        height: 26px;
        background-image: url(../images/icon/icon-arrow-purple.svg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
      }
      .swiper-button-prev {
        margin-left: -48px;
      }
      .swiper-button-next {
        margin-right: -48px;
        transform: rotate(180deg)
      }
    }

    .swiper-rewards {
      position: relative;
      width: calc(100% + 10px);
      margin-left: -5px;
      padding: 5px;
      .swiper-wrapper {
        position: relative;
        width: 100%;
      }
      .swiper-slide {
        cursor: pointer;
        .card {
          box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.16);
          background-color: #ffffff;
          @at-root #{&}__img {
            height: 207px;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
          }
          @at-root #{&}__detail {
            position: relative;
            height: 106px;
            padding: 16px 10px;
          }
          @at-root #{&}__title {
            height: 40px;
            font-size: 20px;
            font-weight: 500;
            color: $brand-color;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          @at-root #{&}__desc {
            position: absolute;
            bottom: 23px;
            left: 10px;
            font-size: 16px;
            font-weight: 400;
            color: #666666;
          }
        }
      }
    }
  }
}

.svgfb {
  position: relative;
  margin-top: -100px;
  height: 100px;
  width: 100%;
  svg {
    height: 100%;
    width: 100%;
    rect {
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 640px) {
  .listicle {
    @at-root #{&}__app {
      width: calc(100% + 48px);
      margin-left: -24px;
    }
    @at-root #{&}__rewards {
      @at-root #{&}__list {
        width: 100%;
        margin-left: 0px;
        .swiper-button-prev {
          display: none;
        }
        .swiper-button-next {
          display: none;
        }
      }
      .swiper-rewards {
        .swiper-wrapper {
          .swiper-slide {
            width: 207px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .listicle {
    @at-root #{&}__header {
      padding: 31px 0 39px 0;
      @at-root #{&}__title {
        font-size: 28px;
        font-weight: 700;
        padding-bottom: 28px;
      }
      @at-root #{&}__detail {
        padding: 0 8px;
        .info {
          @at-root #{&}__icon {
            height: 15px;
            margin-bottom: 8px;
            &.when {
              width: 15px;
            }
            &.where {
              width: 13px;
            }
            &.likes {
              width: 13px;
            }
          }
          @at-root #{&}__text {
            font-size: 15px;
            font-weight: 500;
          }
        }
      }
    }
    @at-root #{&}__app {
      padding: 30px 16px 0 16px;
      height: 336px;
      @at-root #{&}__logo {
        width: 142px;
        height: 35px;
        margin-bottom: 30px;
      }
      @at-root #{&}__dl-text {
        width: 50%;
        font-size: 16px;
        line-height: normal;
        margin-bottom: 24px;
      }
      @at-root #{&}__dl-icon {
        .appstore {
          display: block;
          width: 156px;
          height: 46px;
          margin-bottom: 16px;
        }
        .googleplay {
          display: block;
          width: 156px;
          height: 46px;
        }
      }
      @at-root #{&}__img {
        height: 280px;
        width: 160px;
      }
    }
    @at-root #{&}__desc {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      ol {
        li {
          padding: 32px 0;
          &:before {
            font-size: 28px;
          }
        }
      }
      font[size="5"] {
        font-size: 28px;
      }
    }
    @at-root #{&}__rewards {
      @at-root #{&}__title {
        font-size: 28px;
      }
      .swiper-rewards {
        .swiper-wrapper {
          .swiper-slide {
            width: 160px;
            .card {
              @at-root #{&}__img {
                height: 160px;
              }
              @at-root #{&}__detail {
                height: 78px;
                padding: 12px 8px;
              }
              @at-root #{&}__title {
                font-size: 15px;
              }
              @at-root #{&}__desc {
                left: 8px;
                bottom: 12px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .listicle__app__img {
    height: 260px;
    width: 140px;
  }
}