[class*='col-'] {
    position: relative;
    float: left;
    min-height: 1px;
    max-width: 100%;
}

.col-1 {
    width: calc(100% / 12);
}
.col-2 {
    width: calc(100% / 6);
}
.col-3 {
    width: calc(100% / 4);
}
.col-4 {
    width: calc(100% / 3);
}
.col-5 {
    width: calc(500% / 12);
}
.col-6 {
    width: calc(100% / 2);
}
.col-7 {
    width: calc(700% / 12);
}
.col-8 {
    width: calc(200% / 3);
}
.col-9 {
    width: calc(300% / 4);
}
.col-10 {
    width: calc(500% / 6);
}
.col-11 {
    width: calc(1100% / 12);
}
.col-12 {
    width: 100%;
}

// Gutter
.gutt-10 {
    padding: 0 10px;
}

