// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------

.bg {
  position: relative;
  background-color: #160144;
  overflow: hidden;
  @at-root #{&}--1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(28deg, rgba(154, 82, 184, 0) 0%, #9a52b8 100%);
    opacity: 0.6;
  }
  @at-root #{&}--2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(28deg, rgba(244, 136, 38, 0) 50%, #d84700 100%);
    opacity: 0.4;
  }
}

.explore {
  position: relative;
  @at-root #{&}__bg {
    position: absolute;
    top: 10%;
    right: -44%;
    width: 95%;
    @at-root #{&}-w {
      @include aspect_ratio(1295,1238,'svg');
      svg {
        .bighexagon {
          fill: #9a52b8;
          fill-rule: evenodd;
          opacity: 0.1;
        }
      }
    }
  }
  @at-root #{&}__img--1 {
    position: absolute;
    top: -6%;
    left: -10.5%;
    width: 64.7%;
    transform: translate(-10.5%, -6%);
    z-index: 1;
    .img--1__w {
      @include aspect_ratio(884,878,'.img--1__img');
      .img--1__img {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
      }
    }
  }
  @at-root #{&}__img--2 {
    position: absolute;
    top: 91px;
    right: -77px;
    width: 272px;
    opacity: .2;
    .img--2__w {
      @include aspect_ratio(272,285,'.img--2__img');
      .img--2__img {
        background-image: url(../images/icon/Landing/Pattern-100.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
      }
    }
  }
  @at-root #{&}__text {
    position: absolute;
    top: 50%;
    right: 30px;
    max-width: 420px;
    transform: translateY(-50%);
    z-index: 2;
    @at-root #{&}__title {
      font-family: $text-font-1;
      font-size: 64px;
      line-height: 64px;
      font-weight: 700;
      margin-bottom: 30px;
    }
    @at-root #{&}__desc {
      font-size: 20px;
      line-height: 30px;
      font-weight: 400;
      width: 360px;
    }
  }
  @at-root #{&}__arrow {
    position: absolute;
    width: 64px;
    height: 72px;
    left: 50%;
    bottom: 32px;
    transform: translateX(-50%);
    z-index: 3;
    cursor: pointer;
    svg {
      .arrow-out {
        stroke: #fff;
        stroke-width: 3px;
        fill: none;
        fill-rule: evenodd;
      }
      .arrow-in {
        fill: #fff;
        fill-rule: evenodd;
      }
      &:hover {
        .arrow-out {
          fill: #f58927;
        }
      }
    }
  }
}

