      width: 200%;
      top: 0.5%;
      right: -40%;
      transform: translate(40%, 0.5%);
    }
    @at-root #{&}__text {
      @at-root #{&}__title {
        font-size: 28px;
        line-height: 35px;
        margin-bottom: 14px;
      }
      @at-root #{&}__desc {
        font-size: 16px;
        line-height: 24px;
      }
    }
    @at-root #{&}__img--1 {
      width: 53%;
    }
    @at-root #{&}__img--2 {
      width: 45%;
      top: 44%;
      left: 1.5%;
      transform: translate(-1.5%, -44%);
    }
    @at-root #{&}__img--3 {
      width: 40%;
      top: 59.5%;
      right: 11.5%;
      transform: translate(11.5%, -59.5%);
    }
    @at-root #{&}__img--5 {
      width: 5%;
      top: 45.5%;
      right: 26.5%;
      transform: translate(-26.5%, -45.5%);
    }
    @at-root #{&}__img--6 {
      width: 3%;
      top: 39%;
      left: 26%;
      transform: translate(-26%, -39%);
    }
    @at-root #{&}__img--7 {
      width: 6%;
      top: 28%;
      left: 22%;
      transform: translate(-28%, -22%) rotate(-20deg);
    }
    @at-root #{&}__img--9 {
      width: 40.5%;
      right: 18%;
      top: 21%;
      transform: translate(18%, -21%) rotate(160deg);
    }
    @at-root #{&}__img--10 {
      width: 35%;
      top: 56%;
      right: 5%;
      transform: translate(5%, -56%) rotate(-15deg);
    }
    @at-root #{&}__img--11 {
      width: 39%;
      top: 48.5%;
      left: 0;
      transform: translate(0, -48.5%);
    }
  }
  .subscribe {
    @at-root #{&}__form {
      @at-root #{&}__subtitle {
        margin-bottom: 15px;
      }
      @at-root #{&}-w {
        padding: 24px;
        font-size: 30px;
        font-weight: 700;
        line-height: 40px;
      }
      @at-root #{&}__input {
        width: 100%;
      }
      .input-group {
        font-size: 16px;
        margin-bottom: 10px;
      }
      .wknd-checkbox + label:before {
        margin-right: 17px;
      }
      .wknd-checkbox:checked + label:after {
        height: 5px;
        width: 10px;
        top: 7px;
        left: 7px;
      }
      @at-root #{&}__btn {
        padding: 0 68px;
      }
    }
    @at-root #{&}__img--1 {
      display: none;
    }
    @at-root #{&}__img--2 {
      display: none;
    }
    @at-root #{&}__img--3 {
      display: none;
    }
    @at-root #{&}__img--4 {
      display: none;
    }
    @at-root #{&}__img--5 {
      display: none;
    }
    @at-root #{&}__img--6 {
      width: 60%;
      top: -25%;
      right: -8%;
      transform: translate(8%, -25%);
    }
  }
  .wknd_form {
    input[type=text], input[type=email], input[type=password] {
      width: 85%;
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 360px) {
  .subscribe {
    @at-root #{&}__img--6 {
      width: 60%;
      top: -13%;
      right: -8%;
      transform: translate(8%, -13%);
    }
  }
}