/**
 * Basic typography style for copy text
 */
body{
  color: $text-color;
  font-family: $text-font-2;
  -webkit-font-smoothing: antialiased;
  moz-osx-font-smoothing: grayscale;
}

/**
 * Make different classes, specially to change font
 */

