//   font: normal 125% / 1.4 $text-font-2;
// }

// .font-2{
//   font: normal 125% / 1.4 $text-font-2;
// }

.loaded{
  body{
    font-family: $text-font-1;
  }

  // .font-1{
  //   font: normal 125% / 1.4 $text-font-1;
  // }
  // .font-2{
  //   font: normal 125% / 1.4 $text-font-1;
  // }
}

button, input, optgroup, select, textarea {
  font-family: $text-font-2;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

::-moz-selection {
  background: #160144;
}

::selection {
  background: #160144;
}