.wknd-checkbox {
  position: absolute;
  opacity: 0;
}

.wknd-checkbox + label {
  position: relative;
  padding: 0;
}

.wknd-checkbox + label:before {
  content: '';
  margin-right: 26px;
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  background: transparent;
  border: 1px solid #efefef;
  border-radius: 5px;
  transition: all .1s ease;
}

.wknd-checkbox:hover + label:before {
  background: #f58927;
}

.wknd-checkbox:focus + label:before {
  background: transparent;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
}

.wknd-checkbox:checked + label:before {
  background: #f58927;
}

.wknd-checkbox:disabled + label {
  color: #b8b8b8;
  cursor: auto;
}

.wknd-checkbox:disabled + label:before {
  box-shadow: none;
  background: #ddd;
}

.wknd-checkbox:checked + label:after {
  content: '';
  position: absolute;
  left: 5px;
  top: 9px;
  background: transparent;
  width: 14px;
  height: 7px;
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  transform: rotate(-45deg);
}

.modal {
  display: none;
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

/* Modal Scrollable */
.modal-scrollable {
  position: relative;
  display: block;
  overflow-y: scroll;
  height: 10%;
}

/* Modal Wrapper */
.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  max-height: 70.8%;
  width: 80%;
  border: none;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 2px 8px 1px rgba(51, 51, 51, 0.34);
  padding: 48px;
  margin: auto;
}

/* Modal Title */
.modal-title {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 700;
  color: #0b003e;
  margin-bottom: 30px;
}

/* Modal Content */
.modal-content {
  position: relative;
  display: block;
  font-family: "Rubik", sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #333333;
}

.modal-content p {
  margin: 0;
}

/* The Close Button */
.close {
  position: absolute;
  right: 24px;
  top: 24px;
  width: 20px;
  height: 20px;
  color: #333333;
  overflow: hidden;
}

.close::before, .close::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #000;
}

.close::before {
  transform: rotate(45deg);
}

.close::after {
  transform: rotate(-45deg);
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

