.listicle__header {
  padding: 46px 0 63px 0;
}

.listicle__header__title {
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  font-weight: 700;
  color: #0b003e;
  padding-bottom: 56px;
}

.listicle__header__detail {
  padding: 0 16px;
}

.listicle__header__detail .info__icon {
  height: 30px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  margin-bottom: 20px;
}

.listicle__header__detail .info__icon.when {
  width: 30px;
  background-image: url(../images/icon/icon-date-purple.svg);
}

.listicle__header__detail .info__icon.where {
  width: 26px;
  background-image: url(../images/icon/icon-location-purple.svg);
}

.listicle__header__detail .info__icon.likes {
  width: 26px;
  background-image: url(../images/icon/icon-like-purple.svg);
}

.listicle__header__detail .info__text {
  padding-right: 32px;
  font-size: 24px;
  font-weight: 500;
}

.listicle__desc {
  position: relative;
  max-height: 400px;
  overflow: hidden;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
}

.listicle__desc ol {
  counter-reset: i 0;
  padding: 0;
  margin: 0;
}

.listicle__desc ol li {
  position: relative;
  padding: 50px 0;
  list-style-type: none;
}

.listicle__desc ol li:before {
  content: counter(i) ".";
  counter-increment: i;
  padding-right: 0.5em;
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: #0b003e;
}

.listicle__desc ol li:not(:last-child):after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #f58927;
  background-image: linear-gradient(to left, #160144 0%, #f58927 100%);
  opacity: 0.5;
}

.listicle__desc font[size="5"] {
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: #0b003e;
}

.listicle__desc img {
  width: 100%;
}

.listicle__app {
  position: relative;
