// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

header {
  &.header-landing {
    position: fixed;
    display: block;
    height: 80px;
    width: 100%;
    background-color: transparent;
    color: #ffffff;
    padding: 0 64px;
    z-index: 10;
    .header__nav {
      font-family: $text-font-1;
      float: right;
      > ul {
        display: block;
        list-style: none;
        padding: 0;
        margin: 0;
        font-size: 0;
        height: 80px;
        > li {
          display: inline-block;
          height: 80px;
          line-height: 80px;
          &:not(:last-child) {
            margin-right: 95px;
          }
          > a { 
            color: #ffffff;
            font-size: 18px;
            font-weight: 700;
            transition: all .2s ease;
            &:hover {
              color: #f58927;
            }
            &:focus {
              color: #d94800;
            }
          }
        }
      }
    }
    .header__logo {
      position: relative;
      float: left;
      height: 80px;
      img {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 180px;
        height: 45px;
      }
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 110px;
      background-image: linear-gradient(to top, rgba(22, 1, 68, 0) 0%, rgba(22, 1, 68, 0.75) 68%, #160144 100%);
      opacity: 0;
      transition: all .3s ease;
      z-index: -1;
    }
  }
  &.bg-header {
    &:before {
      opacity: 1;
    }
  }
  &.header-listicle {
    position: relative;
    height: 85px;
    width: 100%;
    background-color: #160144;
    .header-listicle__gradient--1 {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
      width: 100%;
      background-image: linear-gradient(28deg, #9a52b8 0%, #9a52b8 100%);
      opacity: 0.4;
    }
    .header-listicle__gradient--2 {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
      width: 100%;
      background-image: linear-gradient(28deg, rgba(244, 136, 38, 0) 0%, #d84700 100%);
      opacity: 0.4;
    }
    .header-logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 180px;
      height: 45px;
      background-image: url(../images/icon/Header-Logo-Horizontal.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
    }
  }
}

@media screen and (max-width: 768px) {
  header {
    &.header-landing {
      .header__nav {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  header {
    &.header-landing {
      height: 66px;
      padding: 0 16px;
      .header__logo {
        height: 66px;
        img {
          width: 135px;
          height: 34px;
        }
      }
    }
    &.header-listicle {
      height: 62px;
      .header-logo {
        width: 122px;
        height: 30px;
      }
    }
  }
}