.input-group {
  margin-bottom: 25px;
}

// Checkbox
.wknd-checkbox {
  position: absolute;
  opacity: 0;

  & + label {
    position: relative;
    padding: 0;
  }

  & + label:before {
    content: '';
    margin-right: 26px;
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;
    background: transparent;
    border: 1px solid #efefef;
    border-radius: 5px;
    transition: all .1s ease;
  }

  // Box hover
  &:hover + label:before {
    background: #f58927;
  }
  
  // Box focus
  &:focus + label:before {
    background: transparent;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
  }

  // Box checked
  &:checked + label:before {
    background: #f58927;
  }
  
  // Disabled state label.
  &:disabled + label {
    color: #b8b8b8;
    cursor: auto;
  }

  // Disabled box.
  &:disabled + label:before {
    box-shadow: none;
    background: #ddd;
  }

  // Checkmark. Could be replaced with an image
  &:checked + label:after {
    content: '';
    position: absolute;
    left: 5px;
    top: 9px;
    background: transparent;
    width: 14px;
    height: 7px;
    border-left: 2px solid #ffffff;
    border-bottom: 2px solid #ffffff;
    transform: rotate(-45deg);
  }
}